import {AreaSeries, Crosshair, FlexibleWidthXYPlot, GradientDefs, LineSeries} from "react-vis";
import {useState} from "react";

import '../../node_modules/react-vis/dist/style.css';

export default function SparkChart(props) {

    const {chartData} = props;

    const [crosshairValues, setCrosshairValues] = useState([]);

    const onMouseLeave = () => {
        setCrosshairValues([])
    };

    const onNearestX = (value, {index}) => {
        setCrosshairValues([{x: chartData[index].x, y: chartData[index].y}]);
    };

    return <FlexibleWidthXYPlot
        height={30}
        strokeWidth={2}
        margin={{left: 5, right: 5, top: 5, bottom: 5}}
        onMouseLeave={onMouseLeave}
    >
        <GradientDefs>
            <linearGradient id="CoolGradient" x1="0" x2="0" y1="0" y2="1">
                <stop offset="10%" stopColor="#fbca00" stopOpacity={0.3}/>
                <stop offset="100%" stopColor="#1E1E1E" stopOpacity={0.1}/>
            </linearGradient>
        </GradientDefs>
        <AreaSeries
            fill={'url(#CoolGradient)'}
            stroke={"transparent"}
            data={chartData}
            curve="curveNatural"
        />
        <LineSeries
            color={"#fbca00"}
            data={chartData}
            onNearestX={onNearestX}
            curve="curveNatural"
        />
        <Crosshair
            values={crosshairValues}
            style={{
                line: {background: "#fff"}
            }}
            itemsFormat={(values) => {
                return values.map((v, i) => {
                    return {
                        title: "Price",
                        value: v.y
                    };
                });
            }}
            titleFormat={(values) => {
                return {
                    title: "Date",
                    value: values[0].x.toLocaleDateString()
                };
            }}
        />
    </FlexibleWidthXYPlot>;
}