import {
    AppBar,
    Avatar,
    Button,
    CssBaseline,
    Drawer,
    Grid,
    IconButton, Link,
    List, ListItem, ListItemText, ListSubheader,
    ThemeProvider,
    Toolbar, Typography
} from "@mui/material";
import theme from './theme';
import logo from './images/logo.svg';
import {
    HashRouter as Router,
    Routes,
    Route,
    Link as RouterLink
} from "react-router-dom";

import HomePage from "./views/HomePage";
import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import Web3 from "web3";
import useWeb3Modal from "./hooks/useWeb3Modal";
import {
    resetWallet,
    updateWalletAddress,
    updateWalletChain,
    updateWalletProviderType,
} from "./redux/slices/coineusSlice";
import getPairPrices from "./utils/get_prices";
import Staking from "./views/Staking";
import CoineusSales from "./views/CoineusSales";
import CoineusSaleDetails from "./views/CoineusSaleDetails";
import CoineusPortfolio from "./views/Portfolio";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import useDexScreener from "./hooks/useDexScreener";
import Stats from "./views/Stats";
import getUserBalances from "./utils/get_balances";
import EasyBridge from "./views/EasyBridge";
import Admin from "./views/Admin";
import Swap from "./views/Swap";
import Lottery from "./views/Lottery";
import LotteryGame from "./views/LotteryGame";
import Validator from "./views/validators/Validator";
import Raffles from "./views/Raffles";
import RaffleGame from "./views/Raffles/RaffleGame";
import LightningRaffles from "./views/Raffles/LightningRaffles";
import LightningRaffleGame from "./views/Raffles/LightningRaffleGame";

const coineus_fund_address = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";

export default function Coineus() {

    const dispatch = useDispatch();
    const {wallet} = useSelector(state => state.coineus);

    useDexScreener();

    const [menuOpen, setMenuOpen] = useState(false);
    const [web3Modal, setWeb3Modal] = useState(null);
    const web3ModalProvider = useWeb3Modal();

    useEffect(() => {
        if (window.ethereum) {
            connectProvider(window.ethereum)
        }
        changeWeb3Modal();
    }, []);

    async function changeWeb3Modal() {
        const web3mod = await web3ModalProvider.getWeb3Modal();
        if (web3mod.providerController.cachedProvider) {
            const provider = await web3mod.connect()
            await connectProvider(provider)
        }
        setWeb3Modal(web3mod);
    }

    async function connectWallet() {
        const provider = await web3Modal.connect();
        await connectProvider(provider)
    }

    const connectProvider = async (provider) => {
        window.provider = provider
        const web3 = new Web3(provider);
        subscribeProvider(provider)
        const accounts = await web3.eth.getAccounts();
        if (accounts && accounts.length) {
            //set wallet address
            dispatch(updateWalletAddress(accounts[0]))
        }
        const chainId = parseInt(await web3.eth.net.getId())
        dispatch(updateWalletChain(chainId));
        dispatch(updateWalletProviderType(!!provider.bridge ? 'walletconnect' : 'injected'))
    }

    const subscribeProvider = (provider) => {
        provider.on("close", () => {
            clearWallet();
        });

        provider.on("accountsChanged", async (acc) => {
            if (window.provider) {
                dispatch(updateWalletAddress(acc[0]))
            }
        });

        provider.on("chainChanged", async (networkId) => {
            if (window.provider) {
                const chainId = parseInt(networkId)
                dispatch(updateWalletChain(chainId));
            }
        });
    }

    const clearWallet = async () => {
        const web3 = new Web3(window.provider);
        if (web3 && web3.currentProvider && web3.currentProvider.close) {
            await web3.currentProvider.close();
        }
        dispatch(resetWallet())
        await web3Modal.clearCachedProvider();
        window.provider = null;
    }

    //updates pricing
    useEffect(() => {
        getPairPrices(wallet.address);
        const interval = setInterval(()=>getPairPrices(wallet.address), 60000)
        return () => clearInterval(interval);
    }, [wallet.address]);

    //updates balances
    useEffect(() => {
        getUserBalances(wallet.address);
        const interval = setInterval(()=>getUserBalances(wallet.address), 60000)
        return () => clearInterval(interval);
    }, [wallet.address]);

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <Router>
                <AppBar enableColorOnDark>
                    <Toolbar>
                        <IconButton
                            edge="start"
                            color="inherit"
                            aria-label="open drawer"
                            onClick={() => setMenuOpen(true)}
                        >
                            <MenuIcon/>
                        </IconButton>

                        <RouterLink
                            to={`/`}
                            style={{textDecoration: 'none', display: 'flex'}}
                        >
                            <Avatar src={logo} height={40}/>
                        </RouterLink>
                        <Typography variant="h5">Coineus</Typography>
                        <div style={{flexGrow: 1}}/>
                        {
                            wallet.address ?
                                <Button
                                    variant="outlined"
                                    color="inherit"
                                    size="small"
                                    onClick={() => {
                                        clearWallet();
                                    }}
                                >{getChainLogo(wallet.chain)} 0X...{wallet.address?.substr(-6)}</Button>
                                :
                                <Button
                                    variant="outlined"
                                    color="inherit"
                                    size="small"
                                    onClick={() => connectWallet()}>Connect</Button>
                        }
                    </Toolbar>
                </AppBar>
                <Grid container style={{marginTop: 64}}>
                    <Grid item xs={12}>
                        <Routes>
                            <Route
                                path={`/`}
                                exact={true}
                                element={<HomePage/>}
                            />
                            <Route
                                path={`/staking`}
                                exact={true}
                                element={<Staking/>}
                            />
                            <Route
                                path={`/sale`}
                                exact={true}
                                element={<CoineusSales/>}
                            />
                            <Route
                                path={`/sale/:chain/:sales_contract`}
                                exact={true}
                                element={<CoineusSaleDetails/>}
                            />
                            <Route
                                path={`/portfolio`}
                                exact={true}
                                element={<CoineusPortfolio/>}
                            />
                            <Route
                                path={`/stats`}
                                exact={true}
                                element={<Stats />}
                            />
                            <Route
                                path={`/easybridge`}
                                exact={true}
                                element={<EasyBridge />}
                            />
                            <Route
                                path={`/admin`}
                                exact={true}
                                element={<Admin />}
                            />
                            <Route
                                path={`/swap`}
                                exact={true}
                                element={<Swap />}
                            />
                            <Route
                                path={`/lottery`}
                                exact={true}
                                element={<LightningRaffles />}
                            />
                            <Route
                                path={`/raffles`}
                                exact={true}
                                element={<LightningRaffles />}
                            />
                            <Route
                                path={`/lottery/:network/:contract_address`}
                                exact={true}
                                element={<LotteryGame />}
                            />
                            <Route
                                path={`/raffles/:network/:contract_address`}
                                exact={true}
                                element={<LightningRaffleGame />}
                            />
                            <Route
                                path={`/fuse-validators`}
                                exact={true}
                                element={<Validator />}
                            />
                        </Routes>
                    </Grid>
                </Grid>

                <Drawer
                    open={menuOpen}
                    onClose={() => setMenuOpen(false)}
                    PaperProps={{style: {minWidth: 240, backgroundColor: '#000', backgroundImage: 'none'}}}

                >
                    <IconButton onClick={() => setMenuOpen(false)} style={{alignSelf: 'end'}}>
                        <CloseIcon/>
                    </IconButton>
                    <List onClick={() => setMenuOpen(false)}>

                        <ListSubheader>COINEUS.APP</ListSubheader>
                        <ListItem button component={RouterLink} to={'/'}>
                            <ListItemText primary="My Wallet" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/raffles'}>
                            <ListItemText primary="Raffles" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/swap'}>
                            <ListItemText primary="Swap" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/stats'}>
                            <ListItemText primary="Stats" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/staking'}>
                            <ListItemText primary="Stake FUSE" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/easybridge'}>
                            <ListItemText primary="Easy Bridge" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/portfolio'}>
                            <ListItemText primary="Fund Holdings" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>

                        {
                            wallet.address === coineus_fund_address && <ListItem button component={RouterLink} to={'/admin'}>
                                <ListItemText primary="Admin" primaryTypographyProps={{style:{fontWeight:700}}}/>
                            </ListItem>
                        }

                        <ListSubheader>CEUS TOKEN</ListSubheader>
                        <ListItem button component={Link} href={'https://explorer.fuse.io/token/0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8'} target="_blank">
                            <ListItemText primary="Contract" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://app.voltage.finance/#/swap?outputCurrency=0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8'} target="_blank">
                            <ListItemText primary="Buy" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://dexscreener.com/fuse/0x3b46efdd411cc0db43f1be7853727e18cce8b85b'} target="_blank">
                            <ListItemText primary="Chart" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>

                        <ListSubheader>FUSE NETWORK</ListSubheader>
                        <ListItem button component={Link} href={'https://router.via.exchange/avalanche/AVAX/fuse/FUSE'} target="_blank">
                            <ListItemText primary="AVAX Bridge" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://app.voltage.finance/#/bridge'} target="_blank">
                            <ListItemText primary="ETH/BNB Bridge" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://get.fuse.cash/mrxn/46qbc3i4'} target="_blank">
                            <ListItemText primary="fuse.cash" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>

                        <ListSubheader>SOCIAL</ListSubheader>
                        <ListItem button component={Link} href={'https://t.me/coineuschat'} target="_blank">
                            <ListItemText primary="Telegram" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://twitter.com/CoineusCrypto'} target="_blank">
                            <ListItemText primary="Twitter" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://discord.gg/FAsz89g5RT'} target="_blank">
                            <ListItemText primary="Discord" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://www.youtube.com/channel/UCehh09nIOF-2BJ6YyIq3-RQ'} target="_blank">
                            <ListItemText primary="YouTube" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://medium.com/@coineus'} target="_blank">
                            <ListItemText primary="Medium" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://github.com/CoineusCrypto/'} target="_blank">
                            <ListItemText primary="GitHub" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'mailto:contact@coineus.app'} target="_blank">
                            <ListItemText primary="contact@coineus.app" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>


                    </List>
                </Drawer>

            </Router>
        </ThemeProvider>
    )
}

function getChainLogo(id) {
    const size = {width: 16, height: 16}
    const style = {marginRight: 10}
    switch (id) {
        case 1:
            return <Avatar src='https://coineus.app/assets/tokens/eth/logo.png' sx={size} style={style}/>
        case 56:
            return <Avatar src='https://coineus.app/assets/tokens/bnb/logo.png' sx={size} style={style}/>
        case 122:
            return <Avatar src='https://coineus.app/assets/tokens/fuse/logo.png' sx={size} style={style}/>
        case 43114:
            return <Avatar src='https://coineus.app/assets/tokens/avax/logo.png' sx={size} style={style}/>
        default:
            return ''
    }
}