import sale_abi from "../abis/coineus_sale.json";
import token_abi from "../abis/coineus.json";
import Web3 from "web3";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {useDispatch, useSelector} from "react-redux";
import {
    Avatar, Breadcrumbs, Button,
    Container, FormControl,
    Grid, IconButton, InputAdornment,
    ListItem, Link,
    ListItemAvatar,
    ListItemText,
    Paper, TextField, Typography,
} from "@mui/material";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import {copyTextToClipboard} from "../utils";
import {formatWeiToNumber, toWei} from "../utils/format";
import {Link as RouterLink, useParams} from "react-router-dom";
import {
    updateBalances
} from "../redux/slices/coineusSlice";
import CoineusLoading from "../components/CoineusLoading";

export default function CoineusSaleDetails() {

    const dispatch = useDispatch();

    const {chain, sales_contract} = useParams();
    const {wallet, prices, rpc} = useSelector(state => state.coineus);

    const [info, setInfo] = useState({});
    const [amount, setAmount] = useState("");
    const [countdown, setCountdown] = useState("--");
    const [secondsRemaing, setSecondsRemaing] = useState(Infinity);
    const [pending, setPending] = useState(false);

    const web3 = new Web3(rpc[chain]);
    const contract = new web3.eth.Contract(sale_abi, sales_contract);


    //TODO raising in contract
    const forSale = chain === 'avax' ? 45000000 : 1000;

    const getInfo = () => {

        const calls = [
            contract.methods.token().call, contract.methods.ratio().call,
            contract.methods.maxAmount().call, contract.methods.saleOpens().call,
            contract.methods.getAmountPurchased(wallet.address).call,
            contract.methods.canPurchaseAmount(wallet.address).call,]

        makeBatchRequest(web3, calls).then(resp => {

            const [
                token_address,
                ratio,
                maxAmount,
                saleOpens,
                amountPurchased,
                canPurchase
            ] = resp;

            const raising = forSale / ratio;

            const token_contract = new web3.eth.Contract(token_abi, token_address);
            const token_calls = [
                token_contract.methods.name().call,
                token_contract.methods.symbol().call,
                token_contract.methods.decimals().call,
                token_contract.methods.balanceOf(sales_contract).call,
            ]

            makeBatchRequest(web3, token_calls).then(resp => {

                const [
                    token_name,
                    token_symbol,
                    token_decimals,
                    available
                ] = resp;

                setInfo({
                    available: formatWeiToNumber(available),
                    token_address,
                    token_name,
                    token_symbol,
                    token_decimals,
                    ratio,
                    maxAmount: formatWeiToNumber(maxAmount),
                    saleOpens: new Date(saleOpens * 1000),
                    amountPurchased: formatWeiToNumber(amountPurchased),
                    canPurchase: formatWeiToNumber(canPurchase),
                    raising,
                    chain_symbol: chain.toUpperCase()
                })
            });
        })

        getUserInfo();
    }

    useEffect(() => {
        if (wallet.address) {
            getInfo();
            const interval = setInterval(getInfo, 10000)
            return () => clearInterval(interval);
        }
    }, [wallet.address])

    useEffect(() => {
        const interval = setInterval(function () {

            // Get today's date and time
            var now = new Date().getTime();

            // Find the distance between now and the count down date
            var distance = (info.saleOpens) - now;
            setSecondsRemaing(distance);

            // Time calculations for days, hours, minutes and seconds
            var days = Math.floor(distance / (1000 * 60 * 60 * 24));
            var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
            var seconds = Math.floor((distance % (1000 * 60)) / 1000);

            // Display the result in the element with id="demo"
            setCountdown(days + "d " + hours + "h " + minutes + "m " + seconds + "s ");

            // If the count down is finished, write some text
            if (distance < 0) {
                clearInterval(interval);
                setCountdown("Open");
            }
        }, 1000);
        return () => clearInterval(interval);
    }, [info.saleOpens])

    const getUserInfo = () => {
        //FUSE
        const web3 = new Web3('https://rpc.fuse.io');
        web3.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateBalances({
                chain: 'fuse',
                contract_address: 'native',
                balance: resp / (10 ** 18)
            }))
        })

        //AVAX
        const web3AVAX = new Web3('https://api.avax.network/ext/bc/C/rpc');
        web3AVAX.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateBalances({
                chain: 'avax',
                contract_address: 'native',
                balance: resp / (10 ** 18)
            }))
        })

    }


    const purchase = async () => {

        if (!wallet.address) return;

        const web3 = new Web3(window.provider);
        try {
            const gasPrice = await web3.eth.getGasPrice();
            const txData = {
                from: wallet.address,
                value: toWei(amount),
                to: sales_contract,
                gas: 1000000,
                gasPrice
            }

            web3.eth.sendTransaction(txData)
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                    // handleError(error)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                    setAmount('');
                })
                .catch(() => {
                });
        } catch (e) {
            // handleError({message: e.toString()})
        }

    }

    if (Object.entries(info).length === 0) return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Typography textAlign="center">Connect Wallet to see Sale Details</Typography>
            </Grid>
        </Grid>
    </Container>

    return (<Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Breadcrumbs style={{marginBottom: 10}} separator="">
                    <Link to="/" component={RouterLink} underline="hover">Coineus</Link>
                    <Link to="/sale" component={RouterLink} underline="hover">Sales</Link>
                    <Typography>{info.token_name}</Typography>
                </Breadcrumbs>
                <Paper>
                    <ListItem
                        secondaryAction={
                            <IconButton edge="end" aria-label="Copy" onClick={() => {
                                copyTextToClipboard(info.token_address);
                            }}>
                                <ContentCopyIcon/>
                            </IconButton>
                        }
                    >
                        <ListItemAvatar>
                            <Avatar src={`https://coineus.app/assets/tokens/avax/${info.token_address}/logo.png`}/>
                        </ListItemAvatar>
                        <ListItemText
                            primary={`${info.token_address?.substr(0, 6)} ... ${info.token_address?.substr(-6)}`}
                            secondary={`${info.token_symbol} Contract Address`}
                        />
                    </ListItem>
                </Paper>
            </Grid>
            <Grid item xs={12}>
                <Paper>
                    <ListItem>
                        <ListItemText
                            primary={`${info.saleOpens?.toLocaleDateString()} ${info.saleOpens?.toLocaleTimeString()}`}
                            secondary="Sale Opens"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={countdown}
                            primaryTypographyProps={{color: 'primary'}}
                            secondary="Countdown"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(info.available)} ${info.token_symbol}`}
                            secondary="Available for Purchase"
                        />
                    </ListItem>
                </Paper>
            </Grid>
            <Grid item xs={12}>
                <ListItem disableGutters disablePadding>
                    <ListItemText
                        secondary={` ${info.chain_symbol} Balance`}
                    />
                    <ListItemText
                        secondary={`${CoineusCryptoFormat(wallet.balances.avax.native)} ${info.chain_symbol}`}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
                <FormControl fullWidth style={{marginBottom: 20}}>
                    <TextField
                        autoComplete="off"
                        type="number"
                        value={amount}
                        placeholder={`Enter ${info.chain_symbol} Amount`}
                        onChange={(ev) => {
                            if (ev.target.value > info.canPurchase) return;
                            setAmount(ev.target.value)
                        }}
                        InputProps={{
                            endAdornment: <InputAdornment position="end">
                                <Button variant="contained" onClick={() => {
                                    setAmount(wallet.balances.avax.native > info.canPurchase ? CoineusCryptoFormat(info.canPurchase) : CoineusCryptoFormat(wallet.avax_balance))
                                }
                                }>MAX</Button>
                            </InputAdornment>,
                        }}
                    />
                </FormControl>
                <Button fullWidth variant="contained" onClick={purchase} disabled={secondsRemaing > 0 || info.available === 0}>BUY</Button>
            </Grid>
            <Grid item xs={12}>
                <Paper>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(info.amountPurchased)} ${info.chain_symbol} / ${CoineusCryptoFormat(info.amountPurchased * info.ratio)} ${info.token_symbol}`}
                            secondary="Amount purchased"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(info.canPurchase)} ${info.chain_symbol} / ${CoineusCryptoFormat(info.canPurchase * info.ratio)} ${info.token_symbol}`}
                            secondary="Can purchase"
                        />
                    </ListItem>
                </Paper>
            </Grid>
            <Grid item xs={12}>
                <Paper>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(info.raising)} ${info.chain_symbol}`}
                            secondary="Amount to raise"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(info.maxAmount)} ${info.chain_symbol}`}
                            secondary="Max Buy"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`1 ${info.chain_symbol} = ${CoineusCryptoFormat(info.ratio)} ${info.token_symbol}`}
                            secondary="Offer"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(info.raising / forSale)} ${info.chain_symbol}`}
                            secondary={`Price in ${info.chain_symbol}`}
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusUSDFormat((info.raising / forSale) * prices[chain].inUSD)}`}
                            secondary="Price in USD"
                        />
                    </ListItem>
                </Paper>
            </Grid>
        </Grid>
        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Container>)
}