import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import Web3 from "web3";
import {
    Avatar,
    Button,
    Container,
    Fab,
    Grid,
    Link,
    List,
    ListItem, ListItemAvatar,
    ListItemText,
    Paper,
    Skeleton,
    Typography
} from "@mui/material";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import CoineusLoading from "../components/CoineusLoading";
import {useParams, Link as RouterLink} from "react-router-dom";
import lottery2_abi from "../abis/Lottery2.json";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

export default function LotteryGame() {

    let {contract_address, network} = useParams();

    const {wallet, rpc} = useSelector(state => state.coineus);

    const currency = network.toUpperCase();

    const [pending, setPending] = useState(false);

    const [lottery2, setLottery2] = useState({
        ticketCost: 0,
        adminFee: 0,
        lotteryID: 0,
        entriesAllowed: 0,
        nextDraw: 0,
        nextDrawInterval: 0,
        totalFees: 0,
        totalWinnings: 0,
        jackpot: 0,
        players: [],
        previousWinner: '',
        previousJackpot: 0,
        playerStats: {wins: 0, entries: 0, winnings: 0}
    });

    const getLotteryInfo = (contract_address, callback) => {

        if (!wallet.address) return;


        const web3 = new Web3(rpc[network]);
        const lottery = new web3.eth.Contract(lottery2_abi, contract_address);

        const calls = [
            lottery.methods.ticketCost().call,
            lottery.methods.adminFee().call,
            lottery.methods.lotteryID().call,
            lottery.methods.entriesAllowed().call,
            lottery.methods.nextDraw().call,
            lottery.methods.nextDrawInterval().call,
            lottery.methods.totalFees().call,
            lottery.methods.totalWinnings().call,
            lottery.methods.getPlayers().call,
            web3.eth.getBalance(contract_address),
            lottery.methods.getStatsByAddress(wallet.address).call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            const [
                ticketCost,
                adminFee,
                lotteryID,
                entriesAllowed,
                nextDraw,
                nextDrawInterval,
                totalFees,
                totalWinnings,
                players,
                jackpot,
                playerStats
            ] = resp;

            lottery.methods.getGameHistoryByLotteryID(lotteryID-1).call((err, resp) => {
                callback({
                    ticketCost: ticketCost / (10 ** 18),
                    adminFee,
                    lotteryID,
                    entriesAllowed: parseInt(entriesAllowed),
                    nextDraw: nextDraw * 1000,
                    nextDrawInterval,
                    totalFees,
                    totalWinnings: totalWinnings / (10**18),
                    jackpot: jackpot * (1 - adminFee / 100),
                    players,
                    previousWinner: resp.winner,
                    previousJackpot: resp.jackpot,
                    playerStats
                });
            })
        })
    }

    useEffect(() => {
        getLotteryInfo(contract_address, setLottery2);
        const interval = setInterval(() => getLotteryInfo(contract_address, setLottery2), 10000)
        return () => clearInterval(interval);
    }, [rpc[network], wallet.address]);

    const [drawingTime, setDrawingTime] = useState(0);
    useEffect(() => {
        setDrawingTime(msToHMS(timeToNextDraw(lottery2.nextDraw)));
        const interval = setInterval(() => {
            setDrawingTime(msToHMS(timeToNextDraw(lottery2.nextDraw)));
        }, 1000);
        return () => clearInterval(interval);
    })

    const pickWinner = () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(lottery2_abi, contract_address);

        contract.methods.pickWinner("coineus" + (new Date()).getTime())
            .send(
                {
                    from: wallet.address
                }
            )
            .then(function (receipt) {
                getLotteryInfo()
            });

    }

    const enter = async () => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(lottery2_abi, contract_address);
        const gasPrice = await web3.eth.getGasPrice();

        contract.methods.enter()
            .send(
                {
                    from: wallet.address,
                    value: web3.utils.toWei(lottery2.ticketCost.toString()),
                    gas: 300000,
                    gasPrice
                }
            )
            .once('transactionHash', function (tx) {
                setPending(true);
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                getLotteryInfo(contract_address, setLottery2)
                setPending(false);
            });
    }

    const checkChain = () => {
        switch (currency) {
            case "AVAX":
                if (wallet.chain === 43114) return true;
                break;
            case "BNB":
                if (wallet.chain === 56) return true;
                break;
            case "FUSE":
                if (wallet.chain === 122) return true;
                break;
            default:
                return false;
        }
    }

    const changeNetwork = async () => {
        let chainID = 122;
        switch (currency) {
            case "AVAX":
                chainID = 43114;
                break;
            case "BNB":
                chainID = 56;
                break;
            case "FUSE":
                chainID = 122;
                break;
            default:
                return false;
        }

        const web3 = new Web3(window.provider);
        await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{chainId: web3.utils.toHex(chainID)}], // chainId must be in hexadecimal numbers
        });
    }

    const getButtonLabel = () => {
        if (lottery2.lotteryState === 1)
            return "LOTTERY CLOSED";

        if (getOccurrence(lottery2.players, wallet.address) >= lottery2.entriesAllowed)
            return "MAX ENTRIES";

        if (timeToNextDraw(lottery2.nextDraw) < 0)
            return "WAITING TO PICK WINNER";

        return `BUY TICKET (${lottery2.ticketCost} ${currency})`;
    }

    const getButtonDisabled = () => {
        if (lottery2.lotteryState === 1)
            return true;

        if (getOccurrence(lottery2.players, wallet.address) >= lottery2.entriesAllowed)
            return true;

        return timeToNextDraw(lottery2.nextDraw) < 0;
    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12}>
                    <Fab to={"/lottery"} component={RouterLink} color="primary" size="small" style={{position:'fixed', bottom: 20, right: 20}}><ArrowBackIcon /></Fab>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{border: '1px solid #fbca00'}}>
                        <List>
                            <ListItem>
                                <ListItemText primary={`${CoineusCryptoFormat(lottery2.totalWinnings)} ${currency}`}
                                              primaryTypographyProps={{
                                                  style: {fontWeight: 700},
                                                  variant: 'h5',
                                                  color: 'primary',
                                                  textAlign: 'center'
                                              }}
                                              secondary="Total Winnings All-time"
                                              secondaryTypographyProps={{textAlign: 'center'}}
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                {
                    lottery2.ticketCost === 0 && <Grid item xs={12}>
                        <Paper style={{padding:15}}>
                            <Skeleton />
                            <Grid container spacing={2}>
                                <Grid item xs={6}><Skeleton height={70}/></Grid>
                                <Grid item xs={6}><Skeleton height={70}/></Grid>
                            </Grid>
                            <Skeleton height={51.44}/>
                        </Paper>
                    </Grid>
                }

                {
                    lottery2.ticketCost !== 0 && <Grid item xs={12}>
                        <Paper>
                            {
                                (timeToNextDraw(lottery2.nextDraw) > 0) ?
                                <Typography variant="caption" color="primary" style={{
                                    float: "left",
                                    padding: '15px 15px 0 15px'
                                }}>Drawing in {drawingTime}</Typography>
                                : <Typography variant="caption" color="primary" style={{float: "left", padding: '15px 15px 0 15px'}}>Waiting for Winner</Typography>
                            }

                            <Typography variant="caption" color="primary" style={{
                                float: "right",
                                padding: '15px 15px 0 15px'
                            }}>Max Entries: {lottery2.entriesAllowed} - Draw# {lottery2.lotteryID}</Typography>

                            <List>
                                <ListItem>
                                    {
                                        lottery2.entriesAllowed > 1 && <ListItemText
                                            primary={getOccurrence(lottery2.players, wallet.address)}
                                            primaryTypographyProps={{
                                                style: {fontWeight: 700},
                                                variant: 'h5'
                                            }}
                                            secondary="My Entries"/>
                                    }
                                    <ListItemText
                                        primary={`${CoineusCryptoFormat(lottery2.jackpot / (10 ** 18))} ${currency}`}
                                        primaryTypographyProps={{
                                            style: {fontWeight: 700},
                                            variant: 'h5',
                                            textAlign: 'right'
                                        }}
                                        secondary="Current Jackpot"
                                        secondaryTypographyProps={{textAlign: 'right'}}
                                    />
                                </ListItem>
                                { lottery2.lotteryID == 2 && currency === "AVAX" &&
                                    <ListItem>
                                        <ListItemAvatar>
                                            <Avatar
                                                src="https://coineus.app/assets/tokens/avax/0x51e48670098173025C477D9AA3f0efF7BF9f7812/logo.png"/>
                                        </ListItemAvatar>
                                        <ListItemText primary={`${CoineusCryptoFormat(100)} DGNX`}
                                                      primaryTypographyProps={{
                                                          style: {fontWeight: 700},
                                                          variant: 'h5',
                                                          color: '#E76223'
                                                      }}
                                                      secondary={`Bonus Token`}
                                        />
                                    </ListItem>
                                }
                                <ListItem>
                                    {
                                        checkChain()
                                            ?
                                            <Button
                                                fullWidth
                                                variant="contained"
                                                disabled={getButtonDisabled()}
                                                onClick={enter}
                                            >
                                                {getButtonLabel()}
                                            </Button>
                                            :
                                            <Button
                                                fullWidth
                                                variant="contained"
                                                onClick={changeNetwork}>CHANGE NETWORK</Button>
                                    }
                                </ListItem>

                                {
                                    (timeToNextDraw(lottery2.nextDraw) < 0) && <ListItem>
                                        <Button
                                            fullWidth
                                            variant="contained"
                                            onClick={pickWinner}>PICK WINNER</Button>
                                    </ListItem>
                                }

                            </List>
                        </Paper>
                    </Grid>
                }

                <Grid item xs={12}>
                    <Paper>
                        <List>
                            <ListItem>
                                <ListItemText
                                    primary={`0X...${lottery2.previousWinner.substr(-6)}`}
                                    secondary="Previous Winner"
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(lottery2.previousJackpot / (10 ** 18))} ${currency}`}
                                    primaryTypographyProps={{textAlign: 'right'}}
                                    secondary="Amount"
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <List>
                            <Typography style={{marginLeft: 15}}>All-time Stats</Typography>
                            <ListItem>
                                <ListItemText
                                    primary={lottery2.playerStats.entries}
                                    secondary="My Entries"
                                />
                                <ListItemText
                                    primary={lottery2.playerStats.wins}
                                    secondary="My Wins"
                                />
                                <ListItemText
                                    primary={`${CoineusCryptoFormat(lottery2.playerStats.winnings / (10 ** 18))} ${currency}`}
                                    secondary="My Winnings"
                                />
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{padding: 15}}>
                        <List dense disablePadding>
                            {
                                lottery2.players.length > 0 && <>
                                    <Typography>{lottery2.players.length} Entries</Typography>
                                    {
                                        lottery2.players.map((player, i) => {
                                            return <ListItem key={player+""+i} disablePadding>
                                                <ListItemText
                                                    primary={player}
                                                    primaryTypographyProps={{
                                                        color: player.toLowerCase() === wallet.address.toLowerCase() ? "primary" : "",
                                                        variant: "caption",
                                                        fontFamily: 'monospace'
                                                    }}
                                                />
                                            </ListItem>
                                        })
                                    }
                                </>
                            }

                            {
                                lottery2.players.length <= 0 && <ListItem>
                                    <ListItemText primary="No Entries"/>
                                </ListItem>
                            }

                        </List>
                    </Paper>
                </Grid>
            </Grid>
            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Container>
    );
}

function timeToNextDraw(d) {
    return d - new Date();
}

function msToHMS(ms) {
    let h = ms / 3.6e6 | 0;
    let m = (ms % 3.6e6) / 6e4 | 0;
    let s = (ms % 6e4) / 1e3 | 0;
    return `${h}:${('' + m).padStart(2, '0')}:${('' + s).padStart(2, '0')}`;
}

function getOccurrence(array, value) {
    return array.filter((v) => (v === value)).length;
}